@font-face {
  font-family: 'Aller';
  font-style: normal;
  font-weight: 400;
  src: local('Aller'), url(../fonts/Aller.woff2) format('woff2'), url(../fonts/Aller.woff) format('woff');
}



@font-face {
  font-family: 'Aller-Bold';
  font-style: normal;
  font-weight: 400;
  src: local('Aller-Bold'), url(../fonts/Aller-Bold.woff2) format('woff2'), url(../fonts/Aller-Bold.woff) format('woff');
}

@font-face {
  font-family: 'StencilStd';
  font-style: normal;
  font-weight: 400;
  src: local('StencilStd'), url(../fonts/StencilStd.woff2) format('woff2'), url(../fonts/StencilStd.woff) format('woff');
}


@import "resset.scss";
@import "variables.scss";

* {
  font-family: $allerFont;
  font-size: $fontSizeStandart;
}

a, span, div, p, li, b {
  font-family: $allerFont;
}

img {
  width: 100%;
}

p {
  font-size: $fontSizeStandart;
  line-height: 1.4;
  text-align: left;
  color: $colorSecond;
  font-family: $allerFont;
}

h4 {
  font-size: $fontSizeTitlePage;
  color: $colorMain;
  text-align: center;
  line-height: 3;
  font-family: "StencilStd";
  color: #162E53;
  letter-spacing: 3px;
}

h5 {
  font-size: $fontSizeTitleArticle;
  color: $colorSecond;
  text-align: center;
  line-height: 1.2;
  font-family: $allerBoldFont;
  text-transform: uppercase;
  letter-spacing: 0.7px;
}

input, textarea {
  width: 100%;
  border: 1px solid $colorMain;
  line-height: 35px;
  margin-bottom: 20px;
  padding-left: 15px;
  background: transparent;
  font-size: $fontSizeStandart;
  border-radius: 10px;

  &:focus {
    outline: 0;
  }
}

a.logo {
  display: block;
  background: $logo;
  width: $logoWidth;
  height: $logoHeight;
  background-size: 100% 100%;
}

.posRel {
  position: relative;
  
  &>img{
    width: 50vw;
    opacity: 0;
  }
  .imgBloc {
    position: absolute;
    top: 0;
    width: 50vw;
  }
}

.butt, a.butt {
  font-family: "StencilStd";
  display: block;
  background: $colorHover;
  color: $colorSecond;
  width: 100%;
  font-size: $fontSizeButton;
  height: 40px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  font-family: $allerFont;
  box-shadow: 0 11px 41px 0 #0D1F3B;
  border-radius: 10px;
  border: 0;
  letter-spacing: 2px;

  input {
    box-shadow: none;
  }
  &:hover {
    box-shadow: none;
  }
}

button.butt {
  box-shadow: 0 11px 41px 0 #AEAEAE;
  border-radius: 10px;
  font-family: StencilStd;
  padding-top: 2px;
}

.butt span {
  font-family: "StencilStd";
  color: $colorSecond !important;
  padding-right: 40px;
  background-image: url(../img/icons/send.svg);
  background-position: 100% -2px;
  background-size: 20px;
  background-repeat: no-repeat;
  
}

.crumbs {
  margin: 30px 0;


  ul {
    display: flex;
    align-items: center;
  }

  li {
    padding-left: 20px;
    padding-right: 25px;
    background-image: url(../img/icons/crumbs.svg);
    background-repeat: no-repeat;
    background-position: 100% center;

    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
      background-image: none;
    }
  }

  a {
    font-family: $allerFont;
    font-size: 18px;
    color: $colorMain;
    letter-spacing: 1.08px;

    &:hover {
      text-decoration: underline !important;
    }
  }
}

#burger {
  z-index: 10;
  cursor: pointer;
  height: 30px;
  width: 30px;
  text-align: center;
  float: right;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: none;

  &:after,
  &:before {
    content: "";
    height: 2px;
    width: 30px;
    border-radius: 0;
    display: inline-block;
    position: absolute;
    left: 15px;
    background-color: $colorSecond;
    transition: all .2s ease-out;
  }
  &:after {
    bottom: 6px
  }
  &:before {
    top: 6px
  }

  .iconBar {
    width: 30px;
    height: 2px;
    display: inline-block;
    margin: 14px 0;
    background-color: $colorSecond;
    transition: all .2s ease-out;
    border-radius: 3px
  }

  &.burgerOpen .iconBar {
    width: 0
  }
  
  &.burgerOpen:after {
    transform: rotate(-45deg);
    bottom: 14px
  }
  
  &.burgerOpen:before {
    transform: rotate(45deg);
    top: 14px
  }
}


header {
  background: $mainBcg;
  position: fixed;
  top: 0;
  width: 100vw;
  height: $headerHeight;
  z-index: 4;

  .menuMob {
    display: none;
  }

  ul {
    display: flex;
  }

  a, span {
    color: $colorHeader;
    padding: 0 15px;
    font-size: $fontSizeHeader;
    cursor: pointer;

    &:hover {
      color: $colorHeaderHover;
      text-decoration: none;
    }
  }
  li {
    position: relative;

    ul {
      position: absolute;
      background: $bcgSubmenu;
      left: -15px;
      flex-direction: column;
      padding: 0;
      display: none;
      z-index: 1;
      top: $headerHeight - 5;
      box-shadow: 0 2px 20px 0 rgba(39,39,39,0.50);
    border-radius: 10px;

      a, span {
        line-height: 1.6;
        white-space: nowrap;
      }
    }
    &:hover > ul {
      display: flex;
    }
    ul.submenu2 {
      background: $bcgSubmenu2;
      left: 100%;
      top: 0;

      li {
        position: static;
      }
    }
  }

  .menu>li>a svg {
    position: relative;
    top: 2px;
  }

  .menu {

    &>li,
    &>a {
      line-height: $headerHeight;
    }
  }

  .submenu {
    
    a {
      display: block;
      position: relative;

      svg {
        position: absolute;
        right: 15px;
        top: 12px;
      }
    }

    li {
      position: relative;
      padding: 0 15px;

      &:after {
        content: "";
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        height: 2px;
        background-color: $colorHover;
        display: none;
      }
    }
    li:hover {
      background: $mainBcg;

      &:after {
        display: block;
      }
    }
    li, a, span {
      line-height: 40px;
    }
  }

  .submenu2 {
    min-width: 100%;
    li:hover {
      &:after {
        display: none;
      }
    }
  }

  .topbar1 {
    
    &>div {
      display: flex;
      align-items: center;
    }
    .comp {
      margin-top: 5px;
      margin-left: 30px;
      text-transform: uppercase;
      span:hover {
        color: $colorSecond;
      }
    }
    
    .flags {
      margin-top: 5px;
      display: flex;
      justify-content: center;

      span {
        padding: 0;
        border: 1px solid white;
        padding: 3px;
        margin-right: 15px;
        line-height: 1;
      }
      img {
        width: auto;
        height: 15px;
      }
    }
  }

  .menu svg path {
    fill: $colorHeaderHover;
  }
}

main {
  background: $pageBcg;
  padding-bottom: 60px;
}

.banner {

  .slick-list {
    padding: 0 !important;
  }
  .slick-prev {
    left: 15px;
  }
  .slick-next {
    right: 15px;
  }
  img {
    max-height: 100vh;
    object-fit: cover;
    object-position: center;
  }
}

article {
  background: $bcgServicesHome;

  h5 {
    text-align: left;
    margin-bottom: 30px;
    text-transform: uppercase;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: -2px;
      left: -25px;
      width: 15px;
      height: 100%;
      background-image: url(../img/icons/titleMarker.svg);
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }

  p {
    margin-bottom: 30px;
  }

  .descr {
    display: flex;
    flex-direction: column;
  }

  .row>div {
    height: 100%;
  }

  &:nth-child(odd) {
    
    .posRel .imgBloc {
      right: 0;
    }
    .descr {
      padding-left: 50px;
    }
  }
  &:nth-child(even) {

    .row>div:first-child {
      order: 1;
    }
    .posRel .imgBloc {
      left: 0;
    }
    .descr {
      padding-right: 50px;
    }
  }
}

.parteners {
  padding-bottom: 60px;
  .slideParteners {
    position: relative;
  }
  .slideItem {
    padding: 0 30px;
    display: flex;
    align-items: center;
    height: 105px;
    img {
      max-height: 130px;
      width: auto;
    }
  }
  .slick-slide {
    margin-top: 30px;
  }

  .slick-arrow {
    height: 100%;
    width: 30px;
    top: 0;
    background-color: rgba($color: $bcgServicesHome, $alpha: 0.2);
    background-image: url(../img/icons/arrow-slide-red.svg);
    background-size: 15px;
    background-position: center center;

    &:hover {
      background-color: rgba($color: $bcgServicesHome, $alpha: 1);
    }
  }
}

button:focus,
.slideItem:focus,
img:focus {
  outline: 0;
}
.slick-arrow {
  background: transparent;
  border: 0;
  font-size: 0;
  position: absolute;
  top: calc(50% - 27px);
  width: 28px;
  height: 54px;
  background-repeat: no-repeat;
  background-size: 100%;
  transition: all 0.3s ease;
  background-image: url(../img/icons/arrow-slide.svg);
  z-index: 3;
  &:active {
    opacity: 0.7;
    background-size: 90%;
  }
}
.slick-prev {
  left: -50px;
}
.slick-next {
  right: -50px;
  transform: rotate(180deg);
}

footer {
  background: $bcgFooter;
  padding-top: 60px;

  a, li {
    font-size: $fontSizeHint;
    color: $colorSecond;
    line-height: 1.8;
  }
  a:hover {
    color: $colorHeaderHover;
  }

  ul li:first-child {
    text-transform: uppercase;
    margin-bottom: 10px;
    svg {
      display: none;
    }
  }
  p {
    text-align: center;
    margin-top: 30px;
    font-size: $fontSizeHint;
    line-height: 1.6;
  }

  a.logo {
    margin-top: 30px;
  }

  p.prefooter {
    text-align: center;
    padding: 10px 0;
    font-size: $fontSizeHint;
    background: #13233A;
  }
}

section.textBlack {

  a, span, div, p, li, h5 {
    color: $colorBlack;
    text-align: left;
  }
  p {
    margin-bottom: 10px;
  }
}

h5[data-toggle="collapse"] {
  cursor: pointer;
  position: relative;
  color: $colorMain !important;

  svg {
    position: absolute;
    right: 30px;
    top: 3px;
  }
}

.accord {
  position: relative;
  box-shadow: 0 13px 24px 0 rgba(154,154,154,0.50);
  border-radius: 9px;
  padding: 20px;
  padding-bottom: 15px;
  margin-bottom: 30px;
  background: #ffffff;

  &:after {
    content: "";
    position: absolute;
    top: 0px;
    left: 0;
    height: 5px;
    width: 100%;
    background: $colorHover;
    border-top-left-radius: 9px;
    border-top-right-radius: 9px;
  }
  a, li, p, span {
    line-height: 1.4;
  }
  ul,
  .row {
    padding-top: 20px;
  }

  p {
    padding-top: 20px;
  }
}

.parralax {
  margin-top: 30px;
  background-attachment: fixed;
  background-size: cover;
  img {
    opacity: 0;
  }
}

section.imageBack {
  background-image: url(../img/icons/contact-image.png);
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
  padding-bottom: 100px;
}

p.subdescr {
  font-size: $fontSizeTitleArticle;

  strong {
    color: $colorHover;
  }
}

.about-content {
  padding-bottom: 0;
  article {
    padding: 60px 0;
  }
}

.about-content .parralax {
  
  background-image: url("../img/LBV-files-4site/Exploatari-miniere/Carier-2 BADEA/Imagini/FB_IMG_1530135738826.jpg")
}

.contact-content {
  padding-bottom: 0;
  .personal {
    img {
      width: 100px;
      margin-right: 15px;
    }
    .item {
      display: flex;
      margin-bottom: 30px;
    }
  }
  .contactBloc {
    margin-top: 50px;
    p, li, a {
      color: $colorBlack;
    }
    h5 {
      margin-bottom: 20px;
    }
    li {
      line-height: 1.5;
    }
  }
}

.min-content {
  padding-bottom: 0;
  section h5 {
    margin-bottom: 10px;
    margin-top: 30px;
  }

  a {
    display: block;
  }
  a:hover {
    text-decoration: none;
  }
}

.service-content {

  .services {
    margin-bottom: 50px;
  }
  article {
    ul {
      margin-bottom: 30px;
    }
  
    li {
      list-style-type: disc;
      list-style-position: inside;
      color: $colorSecond;
      line-height: 1.3;
    }

    a.dwn {
      color: $colorSecond;
      line-height: 1.3;
      margin-top: 30px;
    }
  }
  .textBlack {
    margin: 60px 0;
  }
