.lightBox {
  .slick-slide {
    margin: 0 30px;
  }

  video {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
  }
  .videoContainer {
    position: relative;
    height: 100%;

    img {
      opacity: 0;
    }
  }
  img {
    cursor: pointer;
  }
  .videoContainer:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-image: url(../img/icons/play-circle-regular.svg);
    background-size: 40px;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 2;
    cursor: pointer;
  }
}

#zoomModal {
  .closeZoom {
    position: fixed;
    top: 25px;
    right: 25px;
    width: 25px;
    height: 25px;
    z-index: 10;
    cursor: pointer!important;

    svg {
      width: 100%;
    }
  }

  .modal-dialog {
    max-width: 100vw;
    max-height: 100vh;
    margin: 0;
  }

  .modalContainer {
    position: relative;
  }

  .zoomSlider {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    background: transparent;

    .slick-arrow {
      background-image: url(../img/icons/arrow-slide-white.svg);
    }
    img {
      max-width: 1200px;
      max-height: 90vh;
      object-fit: cover;
      object-position: center;
    }
    .slick-list {
      padding: 0 !important;
    }
    .slick-slide {
      display: flex;
      align-items: center;
      height: 100vh;
    }
  }

  .modal-content {
    background: transparent;
  }

  .videoContainer {
      width: 100%;
      top: 0;
      right: 0;
      z-index: 1;
      
      img {
          opacity: 0;
      }
      video {
          width: 100%;
          // height: 100%;
          margin-left: auto;
          margin-right: 0;
          object-fit: cover;
          object-position: center center;
      }
  }
}

#requestModal {

  .modal-content {
    padding: 60px;
    background-image: url(../img/icons/modal.png);
    background-size: 100%;
    background-position: 0 bottom;
    background-repeat: no-repeat;
  }
  h4 {
    text-align: left;
    font-family: $allerFont;
  }
  .close {
    position: absolute;
    width: 40px;
    height: 40px;
    top: 20px;
    right: 20px;
    cursor: pointer;
    background-image: url(../img/icons/close.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 30px;
  }
}

.modal-backdrop.show {
  opacity: 0.9;
}

.mapouter{
  position: relative;
  text-align: right;
  height: 500px;
  width: 100%;
  margin-top: 50px;
}
.gmap_canvas {
  overflow: hidden;
  background: none!important;
  height: 500px;
  width: 100%;
}
iframe {
  width: 100%;
}

@media (max-width: 1200px) {
  .zoomSlider {
    max-width: 1000px;
  }
}

@media screen and (max-width: 1200px) and (min-width: 992px) {
  .container {
    max-width: 95vw;
  }
  header {
    a, span {
      font-size: 14px;
    }
    a.logo {
      width: 215px;
      height: 40px;
    }
  }
  p {
    font-size: 14px;
  }
  article {
    padding: 30px 0;
  }
}

@media screen and (max-width: 992px) and (min-width: 768px) {
  article {
    padding: 30px 0;
  }
  p {
    font-size: 14px;
  }
  header {
    height: 130px;

    .topbar1 {

      .comp {
        margin-left: auto;
        margin-right: 0;
        margin-top: 10px;
      }
      
      div:nth-child(3) {
        order: 3;
      }
      div:nth-child(1) {
        order: 1;
      }
      div:nth-child(2) {
        order: 4;
      }
      div:nth-child(4) {
        order: 2;
      }
    }
    li ul {
      top: 68px;
    }
    .menu {
      margin-left: auto;
      margin-right: auto;
    }
  }
  a.logo {
    margin-left: 0;
    margin-right: auto;
    margin-top: 10px;
  }
}

@media (max-width: 768px) {

  body.bodyHidden {
    overflow: hidden;
  }

  h4 {
    line-height: 30px;
    padding: 30px 0;
    padding-bottom: 25px;
    font-size: 25px;
  }

  header {
    height: 120px;
    transition: top .3s ease;

    // .container {
    //   max-width: 100vw;
    // }

    &>.container>.row {
      justify-content: flex-start !important;
    }

    .topbar1 {

      div:first-child {
        margin-bottom: 5px;
      }
      .comp {
        margin-left: auto;
        margin-right: auto;
        padding-right: 20px;
        span {
          white-space: nowrap;
          font-size: 14px;
        }
      }
    }

    .menuMobile .langMobile {
      position: fixed;
      right: calc(16vw - 20px);
      top: 60px;
      line-height: 60px !important;
      transition: top .3s ease;
      

      .submenu {
        position: absolute;
        width: auto;
        height: auto;
        top: 59px;
        left: -15px;
      }
    }

    .menu {
      flex-direction: column;
    }
    .menuMobile,
    .menuMobile .submenu,
    .menuMobile .submenu2 {
      position: fixed;
      width: 300px;
      top: 120px;
      left: -9360px;
      z-index: 2;
    }

    .buttHeader {
      display: block;
      position: relative;

      svg {
        transform: rotate(-90deg);
        position: absolute;
        right: 0;
      }
    }

    .menuMob {
      display: block;

      ul {
        display: block;
        position: fixed;
        width: 300px;
        z-index: 2;
        background: $bcgServicesHome;
        top: 120px;
        left: -360px;
        height: calc(100vh - 120px);
        padding: 30px 0;
        transition: all .3s ease;
      }

      span {
        line-height: 40px;
        color: white;
      }

      .buttHeader + ul li:first-child {
        padding-left: 30px;
        margin-bottom: 20px;

        svg {
          left: 15px;
          top: 16px;
          right: auto;
        }
      }

      li, a {
        line-height: 45px;
        position: relative;
        color: $colorSecond;

        svg {
          position: absolute;
          right: 15px;
          top: 12px;
          transform: rotate(-90deg);
        }
        ul li svg {
          transform: rotate(0deg);
        }
      }
    }
  }

  #cover {
    overflow: hidden;
    width: 100vw;
  }
  header.top {
    top: -58px;

    .menuMobile .langMobile {
      top: 0;
    }

    .menuMob ul {
      top: 60px;
      height: calc(100vh - 60px);
    }
  }

  footer {

    ul li:first-child {
      line-height: 40px;
      position: relative;
      margin-bottom: 0;

      svg {
        display: block;
        position: absolute;
        right: 0;
        top: 13px;
        transition: transform .3s ease;
      }
    }
    ul {
      margin-top: 10px;
      height: 40px;
      overflow: hidden;
      transition: auto .3s ease;
    }
    ul.open {
      height: auto;

      li:first-child svg {
        transform: rotate(-180deg);
      }
    }

    li {
      line-height: 2;
      
    }
  }


  a.logo {
    width: 250px;
    height: 45px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
  }

  #cover {
    overflow: hidden;
  }

  .zoomSlider {
    max-width: 160px;
  }

  #burger {
    display: block;
  }

  .posRel {

    &>img {
      opacity: 1;
      width: 100%;
    }
    .imgBloc {
      display: none;
    }
  }

  article {
    padding: 30px 0;

    .descr {
      margin-top: 30px;
    }
    &:nth-child(odd) {
      .descr {
        padding-left: 25px;
      }
    }
    &:nth-child(even) {
      .descr {
        padding-right: 0;
        padding-left: 25px;
      }
  
      .row>div:first-child {
        order: 1;
      }
      .row>div:last-child {
        order: 1;
      }
    }
  }

  .slick-prev {
    left: 0;
  }
  .slick-next {
    right: 0;
  }

  .crumbs {
    margin: 15px 0;

    ul {
      flex-wrap: wrap;
    }

    li {
      line-height: 1.3;
    }
  }

  .service-content, .miniere-content {

    .services, .textBlack {
      margin-top: 20px;
    }
  }

  h5[data-toggle=collapse] {
    font-size: 16px;
    padding-right: 15px;

    svg {
      right: 0px;
    }
  }

  #requestModal {

    .modal-content {
      padding: 60px 15px;
    }
  }

  .lightBox {

    .slick-slide {
      margin: 0;
    }
    .slick-list {
      padding: 0 !important;
    }
  }
  .slick-arrow {
    width: 20px;
    height: 45px;
  }
}

@media (max-width: 576px) {
  header {
    
    .menuMobile .langMobile {
      right: 10px
    }
  }
}


